import { FarrisContextMenuService } from './context-menu.service';
import { Component, Injector, Input, NgZone, OnInit, Renderer2, OnChanges,
        ViewEncapsulation, ElementRef, ViewChild, ChangeDetectorRef, SimpleChanges, Optional, QueryList, ViewChildren } from '@angular/core';
import { ContextMenuItem } from './menu-item.interface';

@Component({
    selector: 'farris-menus',
    templateUrl: './context-menu.component.html',
    encapsulation: ViewEncapsulation.None,
    exportAs: 'contextMenuRef',
    styles: [
            `
    .f-context-menu-overlay {
        position: fixed;
        z-index: 9999;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
    }
    .f-context-menu-panel {
        position: absolute;
        top: 200px;
        left: 300px;
        background: #fff;
        border: 1px solid #c6c6c6;
        border-radius: 5px;
        box-shadow: 3px 3px 5px #b8b4b4;
        padding: 3px 0px;
        min-width: 180px;
        max-width: 290px;
    }

    .f-context-menu-panel .menu-list {
        list-style: none;
    }

    .f-context-menu-panel .menu-item {
        height: 28px;
        line-height: 28px;
        cursor: pointer;
    }

    .f-context-menu-panel .menu-item.disabled {
        opacity: 0.4;
        cursor: default;
    }

    .f-context-menu-panel .menu-item.menu-sep{
        height: 6px;
        border-top: 1px solid #ccc;
        border-bottom: 1px solid #fff;
        margin-top: 5px;
    }

    .f-context-menu-panel .menu-item.active,
    .f-context-menu-panel .menu-item:hover:not(.menu-sep) {
        background: #e4e7ef;
    }
    .f-context-menu-panel .menu-item .f-context-menu-panel  {
        display: none;
    }

    .f-context-menu-panel .menu-item:hover  .f-context-menu-panel {
        display: block;
    }

    .f-context-menu-panel .menu-item-icon {
        width: 40px;
        text-align: center;
        font-size: 18px;
    }

    .f-context-target-focus {
        position: relative;
        top: 100px;
        left: 0;
        border: 1px dashed;
        height: 29px;
        display: none;
        background: #a1c8fb;
        opacity: 0.2;
    }

    .f-context-menu-panel .menu-item-text .arrow-right {
        width: 32px; height: 100%;display:block; text-align: center;
    }
        `
    ]
})
export class FarrisContextMenuComponent implements OnInit, OnChanges {

    @Input() menuItems: ContextMenuItem[];
    @Input() left: number;
    @Input() top: number;

    @Input() context: any;

    @ViewChild('menuPanel') menuPanel: ElementRef;

    @ViewChildren(FarrisContextMenuComponent) subMenus: QueryList<FarrisContextMenuComponent>;

    parent: FarrisContextMenuComponent;
    id: string;



    constructor(private injector: Injector,
                private ngzone: NgZone,
                private render: Renderer2,
                @Optional() private ctxMenuSer: FarrisContextMenuService,
                private elRef: ElementRef,
                public cd: ChangeDetectorRef) { }

    ngOnInit(): void {

    }

    ngOnChanges(changes: SimpleChanges) {
    }

    onMenuItemClick($event: MouseEvent, item: ContextMenuItem) {
        $event.stopPropagation();

        if (item.disable) {
            return;
        }

        if (item.children && item.children.length) {
            return;
        }

        this.ctxMenuSer.hide();

        if (item.handle) {
            item.handle({ ...(this.context || {}), instance: this, menu: item });
        }

    }

    onMouseEnter($event, item) {
        if (item.children && item.children.length ) {
            const childCtxMenu = this.subMenus.find(m => m.elRef.nativeElement.id === item.id + '_submenu');
            if (childCtxMenu) {
                // childCtxMenu.context = this.context;
                childCtxMenu.parent = this;

                const childMenuPanel = childCtxMenu.menuPanel.nativeElement;
                const panel = this.menuPanel.nativeElement;
                const childContainerWidth = window.innerWidth - panel.offsetLeft - panel.offsetWidth;
                if (childMenuPanel.offsetWidth > childContainerWidth) {
                    const l = -panel.offsetWidth;
                    childMenuPanel.style.left = l + 'px';
                }

                const childContainerHeight = window.innerHeight - childMenuPanel.getBoundingClientRect().top;
                if (childContainerHeight < childMenuPanel.offsetHeight) {
                    const t = window.innerHeight - panel.offsetTop - childMenuPanel.offsetHeight - 10;
                    childMenuPanel.style.top = t + 'px';
                }
            }

        }
    }
}
