import { Directive, HostListener, Input, Injector, ElementRef, OnInit, Renderer2, EventEmitter, Output, Optional } from '@angular/core';
import { cloneDeep } from 'lodash-es';
import { of, Observable } from 'rxjs';
import { FarrisContextMenuService } from './context-menu.service';
import { BeforeShowMenu, ContextMenuItem } from './menu-item.interface';

@Directive({
    selector: '[farris-context-menus]',
})
export class FarrisContextMenuDirective implements OnInit {
    @Input('farris-context-menus') menuItems: ContextMenuItem[];
    @Input() menuClass: string;
    @Input() disabled = false;
    @Input() context: any;
    @Input() beforeShowContextMenu: (e?: any) => Observable<BeforeShowMenu>;
    /** è§¦åå³é®èåå®éDOMæ ç­¾åç§° */
    @Input() activeDomName: string;
    @Input() highlight = true;
    id: string;

    constructor(@Optional() private ctxMenuSer: FarrisContextMenuService,
                private injector: Injector, private elRef: ElementRef, private render: Renderer2) {}

    ngOnInit() {
        this.id = this.contextMenuId();

        if (!this.beforeShowContextMenu) {
            this.beforeShowContextMenu = () => of(true);
        }
    }

    @HostListener('contextmenu', ['$event'])
    public onContextMenu(event: MouseEvent): void {
        if (!this.disabled) {
            let contextMenuDom = null;
            if (this.activeDomName) {
                contextMenuDom = (event.target as any).closest(this.activeDomName);
                if (contextMenuDom) {
                    this.render.addClass(contextMenuDom, 'f-context-menu-active');
                } else {
                    return;
                }
            }

            const beforeShow$ = this.beforeShowContextMenu({ event, contextMenuDom });
            if (beforeShow$ && beforeShow$.subscribe) {
                beforeShow$.subscribe( (e: BeforeShowMenu) => {
                    const ctxData = this.getContextData(e);
                    this.removeSurplusMenus();
                    if (ctxData.show) {
                        document.body.click();
                        this.ctxMenuSer.show({
                            menuItems: this.getViewMenuItems(ctxData.data),
                            event,
                            id: this.id,
                            activeDom: contextMenuDom,
                            context: ctxData.data,
                            menuClass: this.menuClass,
                            target: this.elRef.nativeElement,
                            activeWidth: e['focusTargetWidth'],
                            highlight: this.highlight
                        });

                        event.preventDefault();
                        event.stopPropagation();
                    }
                });
            }
        }
    }

    // å é¤ç¬¬3å±åä»¥åçèåï¼ä»æ¯æä¸¤å±èåå±ç¤º
    private removeSurplusMenus() {
        if (this.menuItems && this.menuItems.length) {
            this.menuItems.forEach((m: any) => {
                if (m !== '-' && m.children && m.children.length) {
                    m.children.forEach(cm => {
                        if (cm !== '-') {
                            cm.children = [];
                        }
                    });
                }
            });
        }
    }

    private contextMenuId() {
        const id = this.elRef.nativeElement.id;
        if (id) {
            return `${id}-context-menu`;
        } else {
            return 'context-menu_' + new Date().getTime();
        }
    }

    private getContextData(e: BeforeShowMenu) {
        if (typeof e === 'boolean') {
            return { show: e, data: null };
        }
        return e;
    }


    private getViewMenuItems(context) {
        if (this.menuItems && this.menuItems.length) {
            return this.checkMenuItems(this.menuItems, context);
        }
        return  [];
    }

    private checkMenuItems(menuItems, context) {
        const menus = menuItems.map((m: any) => {
            if (typeof m === 'string') {
                return m;
            } else {
               const n = this.checkVisibleAndDisable(m, context);
               if (n.children) {
                   n.children = this.checkMenuItems(n.children, context);
               }
               return n;
            }
        }).filter(n => {
            return n === '-' || n.visible;
        });

        if (menus && menus.length) {
            if (menus[0] === '-') {
                menus.shift(0);
            }

            if (menus[menus.length - 1] === '-') {
                menus.pop(0);
            }
        }

        return menus;
    }

    private checkVisibleAndDisable(m: any, context) {
        const n = {...m};
        if (!n.hasOwnProperty('visible')) {
            n.visible = true;
        } else {
            if (typeof n.visible === 'function') {
                n.visible = n.visible( context);
            }
        }

        if (!n.hasOwnProperty('disable')) {
            n.disable = false;
        } else {
            if (typeof n.disable === 'function') {
                n.disable = n.disable( context );
            }
        }

        return n;
    }
}
